  width: 100%;
}
.details_wrap_item {
  padding: 0.3rem;
  background: #fff;
  border-bottom: 0.01rem #dddddd solid;
}
.details_wrap_item .time {
  font-size: 0.32rem;
}
/*  */
.details_wrap_item .time span.cancel {
  float: right;
  font-size: 0.34rem;
  color: #f05a49;
}
/*  */
.details_wrap_item .time span.succeed {
  float: right;
  font-size: 0.34rem;
  color: #00a0e9;
}
/*  */
.details_wrap_item .time span.waiting {
  float: right;
  font-size: 0.34rem;
  color: #29ab91;
}
/*  */
.details_wrap_item .time span.complete {
  float: right;
  font-size: 0.34rem;
  color: #ffb414;
}
.details_wrap_item .date {
  font-size: 0.26rem;
  color: #888;
  margin-top: 0.1rem;
}
.details_wrap_item .content {
  overflow: hidden;
  padding-top: 0.3rem;
  padding-left: 1.92rem;
}
.details_wrap_item .content .pic {
  float: left;
  width: 1.62rem;
  height: 1.61rem;
  margin-left: -1.92rem;
}
.details_wrap_item .content .name {
  font-size: 0.34rem;
}
.details_wrap_item .content .price {
  font-size: 0.28rem;
  margin-top: 0.58rem;
}
.details_wrap_item .content .price span {
  color: #f4333c;
}
.details_wrap_item .content .price span.money {
  font-size: 0.36rem;
}
.details_wrap_time {
  position: relative;
  height: 1.08rem;
  line-height: 1.08rem;
  background: #fff;
  font-size: 0.3rem;
  padding: 0 0.3rem;
}
.details_wrap_time .btn_cancel {
  position: absolute;
  top: 50%;
  right: 0.3rem;
  transform: translateY(-50%);
  display: block;
  width: 1.6rem;
  height: 0.56rem;
  line-height: 0.56rem;
  text-align: center;
  border: 0.01rem #888888 solid;
  border-radius: 0.06rem;
  font-size: 0.3rem;
  color: #666;
}
.details_wrap_reason {
  overflow: hidden;
  padding: 0.3rem;
  background: #fff;
  margin-top: 0.2rem;
}
.details_wrap_reason .why {
  position: relative;
  font-size: 0.34rem;
}
.details_wrap_reason .why .btn_view {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  display: block;
  width: 2.15rem;
  height: 0.56rem;
  line-height: 0.56rem;
  text-align: center;
  border: 0.01rem #888888 solid;
  border-radius: 0.06rem;
  font-size: 0.3rem;
  color: #666;
}
.details_wrap_reason .answer {
  font-size: 0.28rem;
  line-height: normal;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
  margin-top: 0.1rem;
}
.details_wrap_reason .tel {
  font-size: 0.3rem;
  padding-left: 0.1rem;
  margin-top: 0.1rem;
}
.details_wrap_reason .tel .icon_tel {
  display: inline-block;
  width: 0.42rem;
  height: 0.42rem;
  position: relative;
  top: 0.1rem;
  right: 0.1rem;
}
.details_wrap_info {
  padding: 0 0 0.1rem 0.3rem;
  background: #fff;
  margin-top: 0.2rem;
  overflow: hidden;
}
.details_wrap_info .title {
  font-size: 0.34rem;
  margin: 0.22rem 0;
}
.details_wrap_info .picture {
  overflow: hidden;
}
.details_wrap_info .picture img {
  float: left;
  margin: 0 0.2rem 0.3rem 0;
  width: 1.61rem;
  height: 1.61rem;
}
.details_wrap_info .description {
  font-size: 0.32rem;
  margin-bottom: 0.3rem;
}
.details_wrap_info .lable {
  overflow: hidden;
}
.details_wrap_info .lable span {
  float: left;
  display: block;
  width: 2.92rem;
  height: 0.6rem;
  line-height: 0.6rem;
  text-align: center;
  font-size: 0.3rem;
  color: #00a0e9;
  border: 0.01rem #00a0e9 solid;
  margin-bottom: 0.28rem;
  margin-right: 0.5rem;
}
.details_wrap_belongs {
  font-size: 0.28rem;
  color: #888;
  margin: 0.4rem 0 0;
  text-align: center;
  padding-bottom: 1.2rem;
}
.details_wrap_belongs .text:last-child {
  margin-top: 0.1rem;
}
.details_wrap_footbtn {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  background: #00a0e9;
  font-size: 0.3rem;
  color: #fff;
}
.details_shadow {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(51, 51, 51, 0.7);
}
.details_shadow_box {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5.9rem;
  height: 6rem;
  background: #fff;
  border-radius: 0.2rem;
}
.details_shadow_box .title {
  font-size: 0.34rem;
  text-align: center;
  margin-top: 0.5rem;
}
.details_cancelSucceed_box {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0.6rem 1rem;
  background: #fff;
  border-radius: 0.02rem;
  font-size: 0.34rem;
}
/* radio */
.button_holder {
  padding-left: 0.6rem;
  font-size: 0.3rem;
  margin-top: 0.25rem;
}
label {
  display: inline;
}
.regular_radio {
  display: none;
  padding-left: 0.2rem;
}
.regular_radio + label {
  width: 100%;
  display: block;
}
.regular_radio + label i {
  background-color: #fff;
  border: 0.01rem #939393 solid;
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 100%;
  display: inline-block;
  position: relative;
  top: 0.04rem;
  right: 0.2rem;
  margin-top: 0.25rem;
}
.regular_radio:checked + label i:after {
  content: ' ';
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 100%;
  position: absolute;
  top: -0.01rem;
  left: -0.01rem;
  background-color: #fff;
  border: 0.04rem #00a0e9 solid;
}
.button_footer {
  overflow: hidden;
  font-size: 0.34rem;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
}
.btn_cancel {
  float: left;
  width: 50%;
  height: 0.87rem;
  line-height: 0.88rem;
  border-top: 0.01rem #888888 solid;
}
.btn_confirm {
  float: left;
  color: #fff;
  width: 50%;
  height: 0.88rem;
  line-height: 0.88rem;
  background: #00a0e9;
  border-radius: 0 0 0.2rem 0;
}
.details_shadow_code {
  width: 100%;
  height: 6.7rem;
  position: fixed;
  bottom: 0;
  background: #fff;
}
.details_shadow_code .icon_delete {
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
  width: 0.42rem;
